<template>
  <form
    v-on:submit.prevent="onSubmit()"
    class="mt-2 py-5 bg-dark text-white"
    id="antrag"
  >
    <header class="text-center">
      <div v-if="sended == false">
        <h1 class="display-4 py-4 font-weight-light">
          ANTRAG STELLEN
        </h1>
      </div>

      <div v-else>
        <h1 class="display-4 py-4 font-weight-bold text-success">
          IHR ANTRAG WURDE GESENDET
        </h1>
        <img
          src="https://cdn.iconscout.com/icon/free/png-256/right-true-verify-perfect-trust-64-32776.png"
          alt=""
        />
      </div>
    </header>
    <div v-if="sended == false" class="space">
      <h5 id="antrag-info" class="text-center text-secondary ">
        Bitte f√ºllen Sie das nachfolgende Formular aus und wir kontaktieren Sie
        gerne, um weitere Details zu kl√§ren
      </h5>

      <div class="d-flex mb-3 mt-3  justify-content-center ">
        <div class="form-group  form-floating mb-3 col-md-6 col-10 ">
          <input
            type="text"
            class="form-control"
            id="floatingInput"
            v-model="firma"
            placeholder="Firma"
            required="required"
          />
          <label for="floatingInput" style="color:black">Company-name</label>
        </div>
      </div>

      <div class="d-flex mb-3  justify-content-center ">
        <div class="form-group col-md-3 col-5 ">
          <div class="form-floating space1 mb-3">
            <input
              type="text"
              class="form-control "
              placeholder="Vorname"
              v-model="vorname"
              id="floatingInput"
              required="required"
            />
            <label for="floatingInput" style="color:black">Firstname</label>
          </div>
        </div>
        <div class="form-group col-md-3 col-5 ">
          <div class="form-floating space2 mb-3">
            <input
              type="text"
              class="form-control "
              placeholder="Nachname "
              v-model="nachname"
              id="floatingInput"
              required="required"
            />
            <label for="floatingInput" style="color:black">Lastname</label>
          </div>
        </div>
      </div>

      <div class="d-flex mb-3 justify-content-center ">
        <div class="form-group form-floating  mb-3 col-md-6 col-10">
          <input
            type="email"
            class="form-control"
            placeholder="E-Mail"
            id="floatingEmail"
            v-model="mail"
            required="required"
          />
          <label for="floatingEmail" style="color:black">Email</label>
        </div>
      </div>

      <div class="d-flex mb-3 justify-content-center ">
        <div class="form-group form-floating mb-3 col-md-6 col-10">
          <input
            type="number"
            placeholder="Telefonnummer"
            class="form-control"
            id="floatingTel"
            v-model="tel"
            required="required"
          />
          <label for="floatingTel" style="color:black">Telefonnummer</label>
        </div>
      </div>

      <div class="d-flex mb-3 justify-content-center ">
        <div class=" col-md-6 col-10">
          <div class="form-check">
            <input
              class="form-check-input"
              type="checkbox"
              id="flexCheckChecked"
            />
            <label class="form-check-label" for="flexCheckChecked"
              >Ich best√§tige und bin damit einverstanden, dass meine
              pers√∂nlichen Daten verarbeitet werden</label
            >
          </div>
        </div>
      </div>

      <div class="d-flex justify-content-center ">
        <h5 class="text-secondary mb-3">Datenschutz</h5>
      </div>

      <div class="d-flex mb-3 justify-content-center ">
        <div class="justify-content-center">
          <vue-recaptcha
            v-if="clicked"
            ref="recaptcha"
            @verify="onVerify"
            @expired="onExpired"
            :sitekey="sitekey"
          >
          </vue-recaptcha>
        </div>
      </div>

      <div class="d-flex justify-content-center ">
        <button type="submit" v-on:submit="onSubmit()" class="btn btn-danger">
          Anfrage senden
        </button>
      </div>

      <vue-recaptcha
        ref="invisibleRecaptcha"
        @verify="onVerify"
        @expired="onExpired"
        size="invisible"
        :sitekey="sitekey"
      >
      </vue-recaptcha>
    </div>
  </form>
</template>

<script>
import VueRecaptcha from 'vue-recaptcha';

export default {
  data() {
    return {
      firma: '',
      vorname: '',
      nachname: '',
      tel: '',
      mail: '',
      sended: false,
      sitekey: '6LcUcWQaAAAAAIJ9a0k79YiVYlHjCyh6E319VjeO',
      captcha: true,
      clicked: false,
    };
  },
  props: {
    message: {
      type: String,
    },
  },
  components: {
    VueRecaptcha,
  },

  methods: {
    onSubmit() {
      this.clicked = true;
      if (this.captcha == false) {
        this.$emit(
          'onSubmit',
          this.firma,
          this.vorname,
          this.nachname,
          this.tel,
          this.mail,
        );
        this.sended = true;
      }
    },

    onVerify: function(response) {
      this.$refs.invisibleRecaptcha.execute();

      console.log('Verify: ' + response);
      if (response.length != '') {
        this.captcha = true;
      }
      this.captcha = false;
      this.onSubmit();
    },
    onExpired: function() {
      console.log('Expired');
    },
  },
};
</script>
<style>
.space1 {
  margin-right: 5px;
}

.space2 {
  margin-left: 5px;
}
</style>
