<template>
  <div id="app">
    <navbar id="home" />

    <Intro />

    <div id="informationen">
      <Information :images="images" />
    </div>
    <div id="anfrage">
      <formular @onSubmit="addInteressent"> </formular>
    </div>

    <div id="anmeldungen">
      <Anmeldungen
        @getCategorie="getCategorieOfAnmeldung"
        :anzahl="anzahl"
        :cat="category"
        :anm="anmeldungen"
      />
    </div>

    <div id="map">
      <WhereToFind/>
    </div>

  </div>
</template>

<script>
import formular from '@/components/formular.vue';
import Information from '@/components/Information.vue';
import Intro from '@/components/Intro.vue';
import Anmeldungen from '@/components/Anmeldungen.vue';
import navbar from '@/components/navbar.vue';
import WhereToFind from '@/components/WhereToFind.vue';


import axios from 'axios';

export default {
  name: 'app',
  data() {
    return {
      anmeldungen: [],
      anzahl: 0,
      category: [],
      images: [],
    };
  },

  components: {
    formular,
    navbar,
    Information,
    Intro,
    Anmeldungen,
    WhereToFind
    
  },
  created() {
    this.getAnzahlFreiPl√§tze();
    this.getImages();
  },
  methods: {
    async getAnmeldungen() {
      const { data } = await axios({
        url: 'http://127.0.0.1:3000/anmeldungen',
        method: 'GET',
      });
      this.anmeldungen = data;
    },
    async getAnzahlFreiPl√§tze() {
      const { data } = await axios({
        url: '/anmeldungen/anzahl',
        method: 'GET',
      });
      this.anzahl = data;
    },
    async addInteressent(firma, vorname, nachname, tel, mail) {
      await axios({
        url: '/interessenten',
        method: 'POST',
        contentType: 'application/json',
        data: {
          firmen_name: firma,
          mail: mail,
          telefonnummer: tel,
          vorname: vorname,
          nachname: nachname,
        },
      });
    },
    async getCategorieOfAnmeldung(category) {
      const { data } = await axios({
        url: '/anmeldungen/' + category,
        method: 'GET',
      });
      this.category = data;
    },
    async getImages() {
      const { data } = await axios({
        url: '/images', 
        method: 'GET',
      });
      this.images = data;
    },
  },
};
</script>
